import React from "react";
import "./register.css"

class Register extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      name: '',
      email: "",
      password: ""
    }
  }

  onPasswordChange=(event)=>{
    this.setState({password:event.target.value})
  }

  onEmailChange=(event)=>{
    this.setState({email:event.target.value})
  }

  onNameChange=(event)=>{
    this.setState({name:event.target.value})
  }

  onSubmitRegister=()=>{
    fetch("https://face-dtekt-api.herokuapp.com/register", {
      method: "post",
      headers: {"Content-Type" : "application/json"},
      body: JSON.stringify({
        id: "125",
        name: this.state.name,
        email: this.state.email,
        password: this.state.password,
        entries: 0,
        joined: new Date()
      })
    })
      .then(response=>response.json())
      .then(user=>{
        if (user.id){
          this.props.loadUser(user)
          this.props.onRouteChange("home");
        }
      })
  }

  render(){

    // const {onRouteChange} = this.props
    return(
      <article className="br2 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 center bs">
      <main className="pa4 black-80">
        <div className="measure center">
          <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
          <legend className="f4 fw6 ph0 mh0">Register</legend>
          <div className="mt3">
              <label className="db fw6 lh-copy f6" htmlFor="email-address">Name</label>
              <input className="pa2 input-reset ba input-bg hover-bg-black hover-white w-100" 
              type="text" name="name"  id="name" onChange={this.onNameChange} />
          </div>
          <div className="mt3">
              <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
              <input className="pa2 input-reset ba input-bg hover-bg-black hover-white w-100" 
              type="email" name="email-address"  id="email-address" onChange={this.onEmailChange} />
          </div>
          <div className="mv3">
              <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
              <input className="b pa2 input-reset ba input-bg hover-bg-black hover-white w-100" 
              type="password" name="password"  id="password" onChange={this.onPasswordChange} />
          </div>
          </fieldset>
          <div className="">
          <input className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
          type="submit" value="Register" onClick={this.onSubmitRegister}/>
          </div>

        </div>
      </main>
    </article>
    )
  }
}

export default Register